import { html } from 'lit';
import componentStyles from '../../styles/component.styles';
import styles from './form-field-error.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';

/**
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net/?path=/docs/components-form-field-error--docs
 *
 * @slot - The Form field error’s main content.
 *
 * @csspart base - The component's base wrapper.
 *
 */
export default class FormFieldError extends UIKitElement {
  static styles: CSSResultGroup = [styles, componentStyles];

  render() {
    return html`<div part="base" class="form-field-error"><slot></slot></div>`;
  }
}
